/*--------------------------------------------------------------------------------------------
*
*	Icon base styling
*
*--------------------------------------------------------------------------------------------*/
.post-type-acf-field-group i.acf-icon {
	$icon-size: 20px;
	display: inline-flex;
	width: $icon-size;
	height: $icon-size;
	background-color: currentColor;
	border: none;
	border-radius: 0;
	-webkit-mask-size: contain;
	mask-size: contain;
	-webkit-mask-repeat: no-repeat;
	mask-repeat: no-repeat;
	-webkit-mask-position: center;
	mask-position: center;
	text-indent: 500%;
	white-space: nowrap;
	overflow: hidden;
}

/*--------------------------------------------------------------------------------------------
*
*	Icons
*
*--------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {
	i.acf-icon-plus {
		-webkit-mask-image: url("../../images/icons/icon-add.svg");
		mask-image: url("../../images/icons/icon-add.svg");
	}

	i.acf-icon-stars {
		-webkit-mask-image: url("../../images/icons/icon-stars.svg");
		mask-image: url("../../images/icons/icon-stars.svg");
	}

	i.acf-icon-help {
		-webkit-mask-image: url("../../images/icons/icon-help.svg");
		mask-image: url("../../images/icons/icon-help.svg");
	}

	i.acf-icon-key {
		-webkit-mask-image: url("../../images/icons/icon-key.svg");
		mask-image: url("../../images/icons/icon-key.svg");
	}

	i.acf-icon-trash {
		-webkit-mask-image: url("../../images/icons/icon-trash.svg");
		mask-image: url("../../images/icons/icon-trash.svg");
	}

	i.acf-icon-arrow-right {
		-webkit-mask-image: url("../../images/icons/icon-arrow-right.svg");
		mask-image: url("../../images/icons/icon-arrow-right.svg");
	}

	i.acf-icon-arrow-left {
		-webkit-mask-image: url("../../images/icons/icon-arrow-left.svg");
		mask-image: url("../../images/icons/icon-arrow-left.svg");
	}

	i.acf-icon-chevron-right,
	.acf-icon.-right {
		-webkit-mask-image: url("../../images/icons/icon-chevron-right.svg");
		mask-image: url("../../images/icons/icon-chevron-right.svg");
	}

	i.acf-icon-chevron-left,
	.acf-icon.-left {
		-webkit-mask-image: url("../../images/icons/icon-chevron-left.svg");
		mask-image: url("../../images/icons/icon-chevron-left.svg");
	}

	i.acf-icon-key-solid {
		-webkit-mask-image: url("../../images/icons/icon-key-solid.svg");
		mask-image: url("../../images/icons/icon-key-solid.svg");
	}

	i.acf-icon-globe,
	.acf-icon.-globe {
		-webkit-mask-image: url("../../images/icons/icon-globe.svg");
		mask-image: url("../../images/icons/icon-globe.svg");
	}

	i.acf-icon-image,
	.acf-icon.-picture {
		-webkit-mask-image: url("../../images/field-type-icons/icon-field-image.svg");
		mask-image: url("../../images/field-type-icons/icon-field-image.svg");
	}

	/*--------------------------------------------------------------------------------------------
	*
	*	Inactive group icon
	*
	*--------------------------------------------------------------------------------------------*/
	.post-type-acf-field-group {
		.post-state {
			font-weight: normal;

			.dashicons.dashicons-hidden {
				$icon-size: 18px;
				display: inline-flex;
				width: $icon-size;
				height: $icon-size;
				background-color: $gray-400;
				border: none;
				border-radius: 0;
				-webkit-mask-size: $icon-size;
				mask-size: $icon-size;
				-webkit-mask-repeat: no-repeat;
				mask-repeat: no-repeat;
				-webkit-mask-position: center;
				mask-position: center;
				-webkit-mask-image: url("../../images/icons/icon-hidden.svg");
				mask-image: url("../../images/icons/icon-hidden.svg");

				&:before {
					display: none;
				}
			}
		}
	}
}

/*--------------------------------------------------------------------------------------------
*
*	Edit field group page postbox header icons
*
*--------------------------------------------------------------------------------------------*/
#acf-field-group-fields,
#acf-field-group-options {
	.postbox-header,
	.acf-sub-field-list-header {
		h2,
		h3 {
			display: inline-flex;
			justify-content: flex-start;
			align-content: stretch;
			align-items: center;

			&:before {
				content: "";
				$icon-size: 20px;
				display: inline-block;
				width: $icon-size;
				height: $icon-size;
				margin: {
					right: 8px;
				}
				background-color: $gray-400;
				border: none;
				border-radius: 0;
				-webkit-mask-size: contain;
				mask-size: contain;
				-webkit-mask-repeat: no-repeat;
				mask-repeat: no-repeat;
				-webkit-mask-position: center;
				mask-position: center;
			}
		}
	}
}

// Field icon
#acf-field-group-fields .postbox-header h2:before,
h3.acf-sub-field-list-title:before {
	-webkit-mask-image: url("../../images/icons/icon-fields.svg");
	mask-image: url("../../images/icons/icon-fields.svg");
}

// Settings icon
#acf-field-group-options .postbox-header h2:before {
	-webkit-mask-image: url("../../images/icons/icon-settings.svg");
	mask-image: url("../../images/icons/icon-settings.svg");
}

// Layout icon
.acf-field-setting-fc_layout .acf-field-settings-fc_head label:before {
	-webkit-mask-image: url("../../images/icons/icon-layout.svg");
	mask-image: url("../../images/icons/icon-layout.svg");
}

/*--------------------------------------------------------------------------------------------
*
*	Postbox expand / collapse icon
*
*--------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {
	.postbox-header .handle-actions {
		display: flex;

		.toggle-indicator:before {
			content: "";
			$icon-size: 20px;
			display: inline-flex;
			width: $icon-size;
			height: $icon-size;
			background-color: currentColor;
			border: none;
			border-radius: 0;
			-webkit-mask-size: contain;
			mask-size: contain;
			-webkit-mask-repeat: no-repeat;
			mask-repeat: no-repeat;
			-webkit-mask-position: center;
			mask-position: center;
			-webkit-mask-image: url("../../images/icons/icon-chevron-up.svg");
			mask-image: url("../../images/icons/icon-chevron-up.svg");
		}
	}

	// Closed state
	&.closed {
		.postbox-header .handle-actions {
			.toggle-indicator:before {
				-webkit-mask-image: url("../../images/icons/icon-chevron-down.svg");
				mask-image: url("../../images/icons/icon-chevron-down.svg");
			}
		}
	}
}

